import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import styled, { keyframes } from "styled-components";
import { COLORS } from "./../../components/global/constants";

const CartComponent = () => {
  const cartItems = useSelector((state) => state.cart.cartItems);
  const [animate, setAnimate] = useState(0);

  // update a badge number once the items in the cart have changed
  useEffect(() => {
    // prevents the animation from running on the first render
    if (animate === null) {
      setAnimate(0);
      return;
    }
    setAnimate(1);
    const timer = setTimeout(() => {
      setAnimate(0);
    }, 300);
    return () => clearTimeout(timer);
  }, [cartItems]);

  return (
    <BodyStyle>
      <CartImageStyle>
        <svg
          id="cart"
          xmlns="http://www.w3.org/2000/svg"
          viewBox="0 0 122.9 107.5"
          width="20"
          height="20"
          fill="currentColor"
        >
          <path d="M3.9,7.9C1.8,7.9,0,6.1,0,3.9C0,1.8,1.8,0,3.9,0h10.2c0.1,0,0.3,0,0.4,0c3.6,0.1,6.8,0.8,9.5,2.5c3,1.9,5.2,4.8,6.4,9.1 c0,0.1,0,0.2,0.1,0.3l1,4H119c2.2,0,3.9,1.8,3.9,3.9c0,0.4-0.1,0.8-0.2,1.2l-10.2,41.1c-0.4,1.8-2,3-3.8,3v0H44.7 c1.4,5.2,2.8,8,4.7,9.3c2.3,1.5,6.3,1.6,13,1.5h0.1v0h45.2c2.2,0,3.9,1.8,3.9,3.9c0,2.2-1.8,3.9-3.9,3.9H62.5v0 c-8.3,0.1-13.4-0.1-17.5-2.8c-4.2-2.8-6.4-7.6-8.6-16.3l0,0L23,13.9c0-0.1,0-0.1-0.1-0.2c-0.6-2.2-1.6-3.7-3-4.5 c-1.4-0.9-3.3-1.3-5.5-1.3c-0.1,0-0.2,0-0.3,0H3.9L3.9,7.9z M96,88.3c5.3,0,9.6,4.3,9.6,9.6c0,5.3-4.3,9.6-9.6,9.6 c-5.3,0-9.6-4.3-9.6-9.6C86.4,92.6,90.7,88.3,96,88.3L96,88.3z M53.9,88.3c5.3,0,9.6,4.3,9.6,9.6c0,5.3-4.3,9.6-9.6,9.6 c-5.3,0-9.6-4.3-9.6-9.6C44.3,92.6,48.6,88.3,53.9,88.3L53.9,88.3z M33.7,23.7l8.9,33.5h63.1l8.3-33.5H33.7L33.7,23.7z" />
        </svg>
      </CartImageStyle>
      Cart
      <BadgeStyle cartitems={cartItems?.length} animate={animate}>
        {cartItems.length}
      </BadgeStyle>
    </BodyStyle>
  );
};

const animateBadge = keyframes`
  0% { transform: scale(1); }
  50% { transform: scale(1.4); }
  100% { transform: scale(1); }
`;
const BodyStyle = styled.div`
	display: flex;
  position: relative;
  text-decoration: none;
}`;
const CartImageStyle = styled.div`
	margin-right: 10px;
}`;
const NO_NUMBER = 0;
const SHOW_NUMBER = 1;
const BadgeStyle = styled.div`
  display: flex;
  position: absolute;
  top: -8px;
  justify-content: center;
  align-items: center;
  background-color: ${COLORS.buttonBgAlert};
  color: ${COLORS.buttonText};
  border-radius: 50%;
  width: 20px;
  height: 20px;
  font-size: 1rem;
  font-weight: bold;
	opacity: ${(props) =>
    props.cartitems === 0 ? NO_NUMBER : SHOW_NUMBER};
  animation: ${(props) => (props.animate === 1 ? animateBadge : null)} 0.3s ease-in-out;
}`;

export default CartComponent;
